<style scoped>

</style>
<template>
    <div>
        <Row>
            <Col :span="12">
                <h2>报修管理</h2>
            </Col>
            <Col :span="12" style="text-align: right">
                <Button size="large" type="info" @click="showDialog=true"> 我要报修</Button>
            </Col>
        </Row>
        <Divider></Divider>
        <Table stripe border :columns="columns" :data="listData.data"></Table>
        <Spin size="large" fix v-if="load"></Spin>
        <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
            <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
                  :current="listData.current_page" size="small"/>
        </Row>

        <Modal
                v-model="showDialog"
                title="新增保修"
                :loading="loading">
            <Form :model="repair" :label-width="80">
                <FormItem label="描述">
                    <Input v-model="repair.description" type="textarea" :rows="5" placeholder="请填写文字描述"></Input>
                </FormItem>
                <FormItem label="设备">
                    <Select v-model="repair.equipment_no">
                        <Option v-for="(equipment,index) in equipments" v-bind:key="index"
                                :value="equipment.equipment_no" :label="equipment.name"></Option>
                    </Select>
                </FormItem>
                <FormItem label="报修人">
                    <Input v-model="repair.repair_man" placeholder="请填写报修人"></Input>
                </FormItem>
                <FormItem label="联系电话">
                    <Input v-model="repair.mobile" placeholder="请填写联系电话"></Input>
                </FormItem>

            </Form>
            <div slot="footer">
                <Button @click="showDialog=false">取消</Button>
                <Button :loading="loading" type="info" @click="submitData">确认</Button>
            </div>
        </Modal>

    </div>
</template>

<script>
  import api from './api';

  export default {
    data() {
      return {
        listData: [],
        formData: {},
        repair: {},
        equipments: [],
        load: false,
        showDialog: false,
        loading: false,
        columns: [
          {
            title: '报修描述',
            align: 'center',
            key: 'description'
          },
          {
            title: '设备 ID',
            align: 'center',
            key: 'equipment_no'
          },
          {
            title: '报修日期',
            align: 'center',
            key: 'create_time'
          },
          {
            title: '报修人',
            align: 'center',
            key: 'repair_man'
          },
          {
            title: '联系电话',
            align: 'center',
            key: 'mobile'
          },
          {
            title: '状态',
            align: 'center',
            render: (h, params) => {
              let str;
              switch (params.row.status) {
                case 0:
                  str = '待确认';
                  break;
                case 1:
                  str = '已结束';
                  break;
              }
              return h('span', str);
            }
          },
          {
            title: '操作',
            align: 'center',
            render: (h, params) => {
              return h('Button', {
                props: {
                  size: 'small'
                },
                style: {
                  marginRight: '5px',
                  display: params.row.status === 2 ? 'none' : ''
                },
                on: {
                  click: () => {
                    this.cancel(params.row.id);
                  }
                }
              }, '取消');
            }
          }
        ]
      };
    },
    mounted() {
      this.loadData();
      this.loadSelect();
    },
    methods: {
      loadSelect() {
        api.select((res) => {
          this.equipments = res.data.data;
        });
      },
      loadData(page) {
        if (page) {
          this.formData.jump_page = page;
        }
        this.load = true;
        api.index(this.formData, res => {
          this.load = false;
          this.listData = res.data.data;
        }, () => {
          this.load = false;
        });
      },
      cancel(id) {
        this.$Modal.confirm({
          title: '确定取消报修吗?',
          okText: '确定',
          cancelText: '返回',
          loading: true,
          onOk: () => {
            api.cancel({ id: id }, () => {
                this.$Modal.remove();
                this.$Message.success('取消成功');
                this.loadData();
              },
              () => {
                this.$Modal.remove();
              });
          }
        });
      },
      getTime(times) {
        let str = [];
        times.forEach(value => {
          str += (this.times[parseInt(value)].label + '<br/>');
        });
        return str;
      },
      submitData() {
        this.loading = true;
        api.save(this.repair, res => {
          this.loading = false;
          this.showDialog = false;
          this.loadData();
          this.$Message.success('保修成功');
        }, () => {
          this.loading = false;
        });
      }
    }
  };
</script>
